.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: rgba(0, 0, 0, .5);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  width: calc(100% - 10px);
  max-width: 500px;
  z-index: 30;
  text-align: center;
  background-color: #ffa5ba;
  border-radius: 30px;
  padding: 30px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(.5);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
}

.modal.active {
  transform: translate(-50%, -50%)scale(1);
}

.modal__cross {
  width: 30px;
